<template>
  <v-app>
    <v-main>
      <v-container class="text-center">
        <h1>QR Code Scanner</h1>
        <qrcode-stream @decode="onDecode" @init="onInit"></qrcode-stream>
        <v-alert v-if="decodedText" type="success" class="mt-4">
          Decoded: {{ decodedText }}
        </v-alert>
        <v-alert v-if="errorMessage" type="error" class="mt-4">
          {{ errorMessage }}
        </v-alert>
      </v-container>
    </v-main>
  </v-app>
</template>

<script>
import { QrcodeStream } from "vue-qrcode-reader";

export default {
  components: { QrcodeStream },
  data() {
    return {
      decodedText: "",
      errorMessage: "",
    };
  },
  methods: {
    onDecode(decodedString) {
      this.decodedText = decodedString;
      this.errorMessage = "";
    },
    checkCameraSupport() {
    if (!navigator.mediaDevices || !navigator.mediaDevices.getUserMedia) {
      this.errorMessage =
        "Camera access is not supported by your browser. Please try using a modern browser like Chrome or Firefox.";
      return false;
    }
    return true;
  },
  onInit(promise) {
    if (!this.checkCameraSupport()) {
      return;
    }
    promise
      .then(() => {
        console.log("Camera initialized successfully");
      })
      .catch((error) => {
        console.error("Camera initialization error:", error);
        this.errorMessage =
          "Unable to access camera. Please allow camera permissions or check your browser settings.";
      });
  },
  },
};
</script>

<style>
@import "vuetify/dist/vuetify.min.css";
</style>